<template>
  <button
    class="ymu-button"
    :class="[
      'ymu-button--' + type,
      'ymu-button--' + size,
      {
        'ymu-button--radius': radius,
        'ymu-button--disabled': disabled,
        'ymu-button--plain': plain,
        'ymu-button--block': block,
        'ymu-button--inline': !block && inline,
        'ymu-button--round': round,
        'ymu-button--clear': clear,
        'ymu-button--unfeedback': !clickEffect,
        'ymu-button--clickable': !disabled && clickable
      }
    ]"
    :disabled="disabled"
    @click="onClick"
  >
    <i class="ymu-loading" v-if="showLoading"></i>
    <slot>
      <span class="yum-button-text">{{ text }}</span>
    </slot>
  </button>
</template>

<script type="text/javascript">

/**
 * @component ymu-button
 * @param { string } [type=light] -  light primary danger
 * @param { string } [size=normal] -  - large normal small mini
 * @param { boolean } [block=false] - 
 * @param { boolean } [clear=false] - 
 * @param { boolean } [disabled=false] - 
 * @param { boolean } [plain=false] - 
 * @param { boolean } [round=false] - 
 * @param { string } 
 * @param { slot } - 
 */

import use from '../../src/utils/use'
const [useName, useBem] = use('button')

export default useName({
  props: {
    type: {
      type: String,
      default: 'light'
    },
    size: {
      type: String,
      default: 'normal'
    },
    radius: {
      type: Boolean,
      default: true
    },
    inline: {
      type: Boolean,
      default: true
    },
    'click-effect': {
      type: Boolean,
      default: true
    },
    clickable: {
      type: Boolean,
      default: true
    },
    text: String,
    block: Boolean,
    disabled: Boolean,
    plain: Boolean,
    clear: Boolean,
    round: Boolean,
    full: Boolean,
    showLoading: Boolean
  },
  methods: {
    onClick (event) {
      if (this.disabled) return
      this.$emit('click', event)
    }
  }
})
</script>

<style src="./index.scss" lang="scss"></style>



